<template>
        <div class="feature-section">
            <h2 class="left-aligned slide-in-from-right">  TOP5</h2>
            <div class="image-list slide-in-from-left">
              <div class="image-list">
                <div v-for="n in 5" :key="n" class="image-item">
                  <img :src="`https://picsum.photos/479/479?index=${n}`" class="rounded-img"/>
                  <div class="image-info">
                    <div class="info-top">
                      <span class="place-name"> </span>
                      <span class="rating">3.0</span>
                    </div>
                    <div class="address"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="feature-section">
            <h2 class="left-aligned slide-in-from-right">MBTI   TOP5</h2>
            <div class="image-list slide-in-from-left">
              <div v-for="n in 5" :key="n" class="image-item">
                <img :src="`https://picsum.photos/479/479?index=${n+5}`" class="rounded-img"/>
                  <div class="image-info">
                    <div class="info-top">
                      <span class="place-name"> </span>
                      <span class="rating">3.0</span>
                    </div>
                  <div class="address"></div>
                </div>
            </div>
          </div>
        </div>
          <div class="feature-section">
            <h2 class="left-aligned slide-in-from-right">   TOP5</h2>
            <div class="image-list slide-in-from-left">
              <div v-for="n in 5" :key="n" class="image-item">
                <img :src="`https://picsum.photos/479/479?index=${n+10}`" class="rounded-img"/>
                  <div class="image-info">
                    <div class="info-top">
                      <span class="place-name"> </span>
                      <span class="rating">3.0</span>
                    </div>
                  <div class="address"></div>
                </div>
            </div>
          </div>
        </div>
</template>
<script>
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export default {
    name: 'TravelPage',
    mounted() {
    gsap.registerPlugin(ScrollTrigger);
    this.initGSAP();
  },
  methods: {
    initGSAP() {
      function animateFrom(elem, x, y) {
    elem.style.transform = `translate(${x}px, ${y}px)`;
    elem.style.opacity = "0";
    gsap.fromTo(elem, { x: x, y: y, autoAlpha: 0 }, {
      duration: 1.25,
      x: 0,
      y: 0,
      autoAlpha: 1,
      ease: "expo",
      overwrite: "auto"
    });
  }
      function hide(elem) {
        gsap.set(elem, { autoAlpha: 0 });
      }

      gsap.registerPlugin(ScrollTrigger);

      gsap.utils.toArray(".gs_reveal").forEach(function (elem) {
        hide(elem); // assure that the element is hidden when scrolled into view

        ScrollTrigger.create({
          trigger: elem,
          markers: false,
          onEnter: function () { animateFrom(elem, 0, 100) },
          onEnterBack: function () { animateFrom(elem, 0,-100) },
          onLeave: function () { hide(elem) } // assure that the element is hidden when scrolled into view
        });
      });
      
      gsap.utils.toArray(".slide-in-from-left").forEach(function (elem) {
    hide(elem); // assure that the element is hidden when scrolled into view
    ScrollTrigger.create({
      trigger: elem,
      markers: false,
      onEnter: function () { animateFrom(elem, -100, 0) },
      onEnterBack: function () { animateFrom(elem, 100, 0) },
      onLeave: function () { hide(elem) } // assure that the element is hidden when scrolled into view
    });
  });

      gsap.utils.toArray(".slide-in-from-right").forEach(function (elem) {
    hide(elem); // assure that the element is hidden when scrolled into view
    ScrollTrigger.create({
      trigger: elem,
      markers: false,
      onEnter: function () { animateFrom(elem, 100, 0) },
      onEnterBack: function () { animateFrom(elem, -100, 0) },
      onLeave: function () { hide(elem) } // assure that the element is hidden when scrolled into view
    });
  });
    }
  }
    
}
</script>
<style scoped>
     body {
  font-weight: 300;
}


img {
  max-width: 100%;
}

.image-list {
  display: flex; /*    */
  gap: 40px;
}

.rounded-img {
  width: 100%;  /*     100%  */
  height: auto;  /*     */
  border-radius: 10px;  /*     */
  margin-bottom: 10px;
}

.image-list {
  display: grid;  /*      */
  gap: 40px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));  /*   320px    ,   320px     */
}

.image-list img:last-child {
  margin-right: 0; /*      */
}

.left-aligned {
  text-align: left;
}

.feature-section:not(:last-child) {
  margin-bottom: 100px;
}

.image-item {
  display: flex; /*      */
  flex-direction: column; /*    */
}



.info-top {
  display: flex; /*       */
  justify-content: space-between; /*       */
}

.address {
  text-align: left; /*    */
  color:gray
}
.place-name {
  font-weight: bold;
}


</style>